<template>
  <div class="wrapper">
    <!--页脚-->
    <footer class="aui-bar aui-bar-tab" id="footer" style="border-top: 1px solid #f1f1f1;">
      <div class="aui-bar-tab-item" tapmode>
        <img :src="circle_img" style="width: 24px;height: 24px;margin: auto;" v-on:click="clickTab('circle')">

      </div>
      <div class="aui-bar-tab-item" tapmode>
        <img :src="index_img" style="width: 60px;height: 60px;margin: auto;margin-top: -30px;"
             v-on:click="clickTab('index')">
      </div>
      <div class="aui-bar-tab-item" tapmode>
        <img :src="mine_img" style="width: 24px;height: 24px;margin: auto;" v-on:click="clickTab('mine')">

      </div>
    </footer>
  </div>
</template>

<script type="text/ecmascript-6">

  //导入图片
  import circle_n_img from '@/assets/img/circle_n.png'
  import circle_r_img from '@/assets/img/circle_r.png'
  import index_r_img from '@/assets/img/index_r.png'
  import mine_n_img from '@/assets/img/mine_n.png'
  import mine_r_img from '@/assets/img/mine_r.png'

  export default {
    name: 'tab_index',
    data() {
      return {
        circle_img: this.$route.path == '/circle' ? circle_r_img : circle_n_img,
        index_img: index_r_img,
        mine_img: this.$route.path == '/mine' ? mine_r_img : mine_n_img,
      }
    },
    methods: {
      //切换tab
      clickTab(page) {
        switch (page) {
          case 'circle':
            this.$router.push({path: '/circle'})
            break
          case 'index':
            this.$router.push({path: '/index'})
            break
          case 'mine':
            this.$router.push({path: '/mine'})
            break
          default:
            break
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



